@import "react-slideshow-image/dist/styles.css";
body {
  margin: 2;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  
  --background: url(./components/Backgroundimg.svg);
  --profilepicback:url(./components/profileback.svg);
  --introback:url(./components/introback.svg);
  --intro:linear-gradient(to bottom,#FFC8C8,#FFE2E2,#f6f6f6,#FFE2E2,#FFC8C8,#FF9999);
  --color1:#f6f6f6;
  --color2:#FFE2E2;
  --color3:#FF9999;
  --color4:#444F5A;
  --color5:#3E4149;
  --color6:#FFE2E2;
  --color7:#3E4149;
  --color8:#FFC8C8;
  --color9:#AAAAAA;
  background: var(--background);

}
.footer-distributed{
	background-color:#3E4149;
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
	box-sizing: border-box;
	width: 100%;
	text-align: left;
	font: bold 16px sans-serif;
	padding: 20px 40px;
}

.footer-left h3 img{
	width: 15%;
	margin: 3%;
	margin-left: 10%;
}

.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right{
	display: inline-block;
	vertical-align: top;
}

/* Footer left */

.footer-distributed .footer-left{
	width: 40%;
}

/* The company logo */

.footer-distributed h3{
	color:  #FFE2E2;
	font: normal 36px 'Open Sans', cursive;
	margin: 0;
}

.footer-distributed h3 span{
	color:  #FF9999;
}

/* Footer links */

.footer-distributed .footer-links{
	color:   #FFE2E2;
	margin: 20px 0 12px;
	padding: 0;
}

.footer-distributed .footer-links a{
	display:inline-block;
	line-height: 1.8;
  font-weight:5%;
	text-decoration: none;
	color:  inherit;
	margin-left:3%;
	margin-right:3%;
}

.footer-distributed .footer-company-name{
	color:  #222;
	font-size: 14px;
	font-weight: normal;
	margin: 0;
}

/* Footer Center */

.footer-distributed .footer-center{
	width: 35%;
}

.footer-distributed .footer-center i{
	background-color:  #33383b;
	color: #ffffff;
	font-size: 25px;
	width: 38px;
	height: 38px;
	border-radius: 50%;
	text-align: center;
	line-height: 42px;
	margin: 10px 15px;
	vertical-align: middle;
}

.footer-distributed .footer-center i.fa-envelope{
	font-size: 17px;
	line-height: 38px;
}

.footer-distributed .footer-center p{
	display: inline-block;
	color: #FFE2E2;
  font-weight:400;
	vertical-align: middle;
	margin:0;
}

.footer-distributed .footer-center p span{
	display:block;
	font-weight: normal;
	font-size:14px;
	line-height:2;
}

.footer-distributed .footer-center p a{
	color: #FF9999;
	text-decoration: none;
}

.footer-distributed .footer-links a:before {
  content: "|";
  font-weight:300;
  font-size: 20px;
  left: 0;
  display: inline-block;
  padding-right: 5px;
}

.footer-distributed .footer-links .link-1:before {
  content: none;
}
.footer-links .link-2:hover{
	color: #FF9999;
}

.footer-links .link-1:hover{
	color: #FF9999;
}
/* Footer Right */

.footer-distributed .footer-right{
	width: 20%;
}

.footer-distributed .footer-company-about{
	line-height: 20px;
	color:  #FFE2E2;
	font-size: 13px;
	font-weight: normal;
	margin: 0;
}

.footer-distributed .footer-company-about span{
	display: block;
	color:  #FF9999;
	font-size: 14px;
	font-weight: bold;
	margin-top: 3%;;
	margin-bottom: 20px;
}

.footer-distributed .footer-icons{
	margin-top: 25px;
}

.footer-distributed .footer-icons a{
	display: inline-block;
	width: 35px;
	height: 35px;
	cursor: pointer;
	background-color:  #33383b;
	border-radius: 5px;

	font-size: 20px;
	color: white;
	text-align: center;
	line-height: 35px;

	margin-right: 3px;
}
.footer-distributed .footer-icons a:hover{
	background-color: #222;
}

.logos img:hover{
	transform: scale3d(1.1,1.1,1.1);
  }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
